import "../authentication/authentication.css";
import GoogleBtn from "../../components/Social-btn/GoogleBtn";
import FacebookBtn from "../../components/Social-btn/FacebookBtn";
import FormInput from "../../components/shared/formInput";
import { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "../../redux/User";
import { State } from "../../redux/Types";
import { axiosPost } from "../../services/axiosService";
import { getCookie, setCookie } from "../../services/cookieService";
import { Link, useNavigate, useLocation } from "react-router-dom";
import Loader from "../../components/loaders/Loader";
import { AiChatBotShortLogo } from "../../assets/AiBotAssets";
import environment from "../../constants/Environment";
import validateData from "../../services/validationService";
import { TabTitle } from "../../utils/TitleFuntion";
import { Crisp } from "crisp-sdk-web";

function Login() {
  const environmentData = environment();
  const inputRef = useRef(null);
  const dispatch = useDispatch();
  const location = useLocation();
  const route = useNavigate();
  let userOnBoardingDone = getCookie("onBoarding");
  const queryEmail = new URLSearchParams(location.search).get("email");
  const isLoggedIn = useSelector((state: State) => state.user.isLoggedIn);
  const defaultComp = useSelector((state: State) => state.user.defaultCompany);
  const subdomain = useSelector(
    (state: State) => state.user.companyDetails.subDomain
  );
  const sessionCount = useSelector((state: State) => state.user.sessionCount);
  const [email, setEmail] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [emailEmpty, setEmailEmpty] = useState<boolean>(false);
  const [passwordEmpty, setPasswordEmpty] = useState<boolean>(false);
  const [isEmailValid, setEmailValid] = useState<boolean>(true);
  const [isPasswordValid, setPasswordValid] = useState<boolean>(true);
  const [loader, setLoader] = useState(false);
  const [showLoader, setShowLoader] = useState<boolean>();
  const [errMessage, setErrMessage] = useState<string>("");

  useEffect(() => {
    TabTitle(" | Start");
    var validator = new RegExp(
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
    if (queryEmail && queryEmail.trim().length >= 1) {
      if (validator.test(queryEmail as string)) {
        setEmailValid(true);
        setEmail(queryEmail as string);
      }
    }
  }, []);

  useEffect(() => {
    if (showLoader) {
      setTimeout(() => {
        setShowLoader(false);
      }, 3000);
    }
  }, [showLoader]);

  useEffect(() => {
    if (isLoggedIn) {
      if (userOnBoardingDone.trim().length <= 1 || sessionCount >= 1) {
        if (subdomain !== "") {
          if (defaultComp) {
            route("/dashboard");
          } else {
            route("/switchAccount");
          }
        }
      }
    }
  }, [userOnBoardingDone, sessionCount]);

  useEffect(() => {
    if (isLoggedIn) {
      route("/dashboard");
    }
  }, [isLoggedIn]);

  // useEffect(() => {
  //   const keyDownHandler = (event) => {
  //     if (event.key === "Enter") {
  //       event.preventDefault();
  //       handelLogin();
  //     }
  //   };

  //   document.addEventListener("keydown", keyDownHandler);

  //   return () => {
  //     document.removeEventListener("keydown", keyDownHandler);
  //   };
  // }, []);

  useEffect(() => {
    const validEmail = validateData(email, "email");
    if (email.length) setEmailEmpty(false);
    if (validEmail) setEmailValid(true);
    if (password.length) setPasswordEmpty(false);
  }, [email, password]);

  async function handelLogin() {
    const emailValue = email ? email : emailRef?.current?.value ?? "";
    const passValue = password ? password : passwordRef?.current?.value ?? "";

    const validEmail = validateData(emailValue, "email");
    const validPassword = validateData(passValue, "password");

    // if (!validEmail || !validPassword) return;

    try {
      if (loader) return;
      if (!emailValue || !passValue)
        throw new Error("Please fill all the required data");
      setLoader(true);

      const response = await axiosPost("/auth/login", {
        email: emailValue.trim(),
        password: passValue.trim(),
      });
      const { userData, companyData, defaultCompany, token, thriveData } =
        response.data.data;

      const manualThriveWidgetLoad = (thriveData) => {
        var trackingScript = document.createElement("script");
        trackingScript.type = "text/javascript";
        trackingScript.text = `
          var ztUserData = {};
          ztUserData['za_email_id'] = '${thriveData?.userEmail}';
          ztUserData['user_unique_id'] = '${
            thriveData?.userEmail.split("@")[0]
          }';
          ztUserData['thrive_digest'] = '${thriveData?.hmacId}';
          ztUserData['thrive_ref_id'] = '${localStorage.getItem(
            "thrive_ref_id"
          )}';
          ztUserData['signUpPage'] = 'https://app.outgrow.chat/register';
          ztUserData['signInPage'] = 'https://app.outgrow.chat/login';`;
        document.head.appendChild(trackingScript);
        console.log("lohgin script", trackingScript);
        if (typeof window.reloadThriveWidget === "function")
          window.reloadThriveWidget();
      };
      manualThriveWidgetLoad(thriveData);

      setCookie("token", token, 1, environmentData?.PUBLIC_DOMAIN, "/");
      setCookie("token", token, 1, environmentData?.PAGE_DOMAIN, "/");
      // dispatch(userSlice.actions.setThrive(true));
      // dispatch(userSlice.actions.setThriveData({
      //   email: thriveData.userEmail,
      //   digest : thriveData.hmacId,
      //   userId : thriveData.userEmail.split(/[^a-zA-Z0-9]+/)[0]
      // }))

      Crisp.setTokenId(userData._id);
      Crisp.session.reset();
      Crisp.user.setEmail(userData.userEmail);

      dispatch(
        userSlice.actions.login({
          userId: userData._id,
          userEmail: userData.userEmail,
          userName: userData.userName,
          userPhone: userData.userPhone,
          sessionCount: userData.sessionCount,
          defaultCompany: defaultCompany,
          companyDetails: {
            _id: companyData._id,
            compName: companyData.compName,
            userId: companyData.userId,
            count: companyData.count,
            subDomain: companyData.subDomain,
            createdAt: companyData.createdAt,
            updatedAt: companyData.updatedAt,
          },
          showCoupon: userData.showCoupon ?? true,
        })
      );
      setLoader(false);
    } catch (err: any) {
      console.log(err);
      setErrMessage(
        err.response?.data.message ?? err.message ?? "Something went wrong"
      );
      setLoader(false);
    }
  }

  const validateEmailOnBlur = () => {
    const validEmail = validateData(email, "email");
    if (email.length) {
      validEmail ? setEmailValid(true) : setEmailValid(false);
      setEmailEmpty(false);
    } else {
      setEmailEmpty(true);
    }
  };

  const emailRef = useRef<HTMLInputElement>(null);
  const passwordRef = useRef<HTMLInputElement>(null);

  return (
    <div>
      {/* For Loader */}
      {showLoader && <Loader />}
      <div
        className={
          showLoader
            ? "main_container flex-cnt none padding-block"
            : "main_container flex-cnt padding-block"
        }
      >
        <div className="omni-logo">
          {environment().isAI ? (
            <AiChatBotShortLogo />
          ) : (
            <svg
              width="54"
              height="54"
              viewBox="0 0 68 68"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M45.3741 30.4054C47.512 29.4679 50.0516 30.4409 50.5457 32.7226C51.2867 36.1448 51.1705 39.7205 50.176 43.1238C48.7834 47.8892 45.7576 52.0123 41.6292 54.7701C37.5008 57.5279 32.5334 58.7443 27.5979 58.206C22.6624 57.6677 18.0739 55.4091 14.6371 51.8262C11.2003 48.2433 9.13443 43.5649 8.80187 38.6113C8.46931 33.6577 9.89124 28.7451 12.8183 24.735C15.7454 20.7248 19.9908 17.8732 24.81 16.68C28.2517 15.8279 31.8292 15.8605 35.2176 16.7432C37.4767 17.3317 38.3433 19.9096 37.3177 22.0067V22.0067C36.292 24.1038 33.7439 24.891 31.4266 24.6081C29.9056 24.4223 28.3519 24.5123 26.8418 24.8862C23.9502 25.6021 21.403 27.3131 19.6468 29.7192C17.8905 32.1253 17.0373 35.0728 17.2369 38.045C17.4364 41.0171 18.6759 43.8242 20.738 45.9739C22.8001 48.1237 25.5532 49.4788 28.5145 49.8018C31.4758 50.1248 34.4563 49.3949 36.9333 47.7403C39.4103 46.0856 41.2258 43.6118 42.0613 40.7525C42.4977 39.2592 42.6523 37.7107 42.5299 36.1832C42.3436 33.8562 43.2361 31.3429 45.3741 30.4054V30.4054Z"
                fill="#FB5F66"
              ></path>
              <path
                d="M53.1185 23.6639C52.3799 24.2422 50.1861 23.4112 47.814 22.3194C45.9231 23.0419 44.7003 22.6322 47.2581 25.3863C49.1221 27.3934 43.7167 27.1529 40.5963 30.2732C36.5793 34.4384 38.3334 43.7882 33.3978 35.9912C31.538 33.0532 34.1797 42.0226 32.9266 42.3874C31.2648 42.6672 31.4684 40.6416 30.8727 35.8983C30.5888 33.6373 30.2527 35.6886 29.8491 38.1711C31.5761 42.301 31.0061 39.5987 29.7744 41.0952L29.4155 40.6893C29.1159 42.237 28.7841 43.3955 28.4158 43.0272C27.5652 42.1765 26.7057 37.7753 25.551 36.3197L25.3955 36.144C25.4479 36.1964 25.4997 36.2552 25.551 36.3197L29.4155 40.6893C29.5682 39.9008 29.7125 39.0113 29.8491 38.1711C29.3228 36.9127 28.5833 35.0199 27.5843 32.1807C26.5209 27.9698 33.7239 30.3348 36.6549 27.996C39.586 25.6572 40.7038 22.6146 40.2445 19.5203C40.3024 18.4608 44.2585 20.6829 47.814 22.3194C48.4192 22.0881 49.0928 21.7408 49.7329 21.1367C50.1888 20.7063 50.5495 20.2429 50.7828 19.7461L46.7626 13.0023C50.7998 15.6984 51.6303 17.9421 50.7828 19.7461L53.1185 23.6639Z"
                fill="#444444"
              ></path>
              <circle
                cx="47.0261"
                cy="20.4576"
                r="7.51467"
                transform="rotate(-135 47.0261 20.4576)"
                fill="#444444"
              ></circle>
              <circle
                cx="30.155"
                cy="37.3282"
                r="9.0176"
                transform="rotate(-135 30.155 37.3282)"
                fill="#444444"
              ></circle>
            </svg>
          )}
          <h1>{environment().isAI ? "Chatbot Builder" : "Omniengage"}</h1>
        </div>
        <div className="right_container">
          <div className="login-container">
            <div className="head-social-btn ">
              <div className="head">
                <h1>
                  Welcome Back to{" "}
                  {environment().isAI ? "Chatbot Builder" : "Omniengage"}
                </h1>
                <span>Use your credentials to login</span>
                {/* <p>Enter your credentials to create your account</p> */}
              </div>

              <div className="social-btn">
                <GoogleBtn />
                <FacebookBtn />
              </div>
            </div>
            <div className="or-login-container">
              <div className="line-separator"></div>
              <div className="or-label">or</div>
              <div className="line-separator"></div>
            </div>

            {/* <div className="form-group">
              <label htmlFor="">E-Mail</label>
              <input type="text" className="form-control" required/>
            </div> */}
            {errMessage && (
              <div className="login-error flex-cnt gap-5">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="12"
                  height="13"
                  viewBox="0 0 12 13"
                  fill="none"
                >
                  <g id="Frame" clipPath="url(#clip0_5089_11176)">
                    <g id="Group">
                      <path
                        id="Vector"
                        d="M11.6236 7.30127L8.71251 2.10931C7.52718 -0.00473022 4.47517 -0.00564575 3.28938 2.10931L0.378281 7.30127C-0.717788 9.25629 0.699814 11.6701 2.93736 11.6701H9.06453C11.325 11.6701 12.7079 9.23514 11.6236 7.30127ZM9.06453 10.7329H2.93736C1.40276 10.7329 0.456833 9.0773 1.19566 7.75958L4.10677 2.56763C4.93486 1.0907 7.06685 1.09033 7.89513 2.56763L10.8062 7.75958C11.5459 9.07867 10.5977 10.7329 9.06453 10.7329ZM6.48723 3.78391V6.54843C6.48723 6.80725 6.27748 7.017 6.01866 7.017C5.75993 7.017 5.55009 6.80725 5.55009 6.54843V3.78391C5.55009 3.52518 5.75993 3.31534 6.01866 3.31534C6.27748 3.31534 6.48723 3.52518 6.48723 3.78391ZM6.46956 8.39926V8.86005C6.46956 9.11877 6.25972 9.32852 6.00099 9.32852C5.74217 9.32852 5.53242 9.11877 5.53242 8.86005V8.39926C5.53242 8.14044 5.74217 7.93069 6.00099 7.93069C6.25972 7.93069 6.46956 8.14044 6.46956 8.39926Z"
                        fill="#FB5F66"
                      />
                    </g>
                  </g>
                  <defs>
                    <clipPath id="clip0_5089_11176">
                      <rect
                        width="12"
                        height="12"
                        fill="white"
                        transform="translate(0 0.5)"
                      />
                    </clipPath>
                  </defs>
                </svg>
                {/* Email or password is incorrect ! Please try again. */}
                {errMessage}
              </div>
            )}

            <FormInput
              emailRef={emailRef}
              labelName={"Email"}
              inputType={"text"}
              placeHolder={"Enter email"}
              isRequired={true}
              onDataChange={(e) =>
                setEmail(e.target.value.trim().toLowerCase())
              }
              value={email}
              onBlur={validateEmailOnBlur}
              onKeyDown={handelLogin}
            />
            {emailEmpty ? (
              <p className="message">
                <svg
                  width="11"
                  height="11"
                  viewBox="0 0 11 11"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09603 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802V9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099V7.24099Z"
                    fill="#E31E26"
                  />
                </svg>
                Field is required
              </p>
            ) : isEmailValid ? (
              <></>
            ) : (
              <p className="message">
                <svg
                  width="11"
                  height="11"
                  viewBox="0 0 11 11"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09603 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802V9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099V7.24099Z"
                    fill="#E31E26"
                  />
                </svg>
                Enter a valid email
              </p>
            )}

            <FormInput
              passwordRef={passwordRef}
              labelName={"Password"}
              inputType={"password"}
              placeHolder={"Enter password"}
              span={"Forgot password?"}
              isRequired={true}
              onDataChange={(e) => setPassword(e.target.value.trim())}
              value={password}
              onBlur={() =>
                !password.length
                  ? setPasswordEmpty(true)
                  : setPasswordEmpty(false)
              }
              onKeyDown={handelLogin}
            />
            {passwordEmpty && (
              <p className="message message-p">
                <svg
                  width="11"
                  height="11"
                  viewBox="0 0 11 11"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09603 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802V9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099V7.24099Z"
                    fill="#E31E26"
                  />
                </svg>
                Field is required
              </p>
            )}

            <div className="auth-btn">
              <input
                type="submit"
                disabled={
                  !email ||
                  !password ||
                  emailEmpty ||
                  passwordEmpty ||
                  !isEmailValid ||
                  loader
                }
                onClick={handelLogin}
                className="btn"
                value={loader ? "Loading..." : "Log in"}
                ref={inputRef}
              />
              <p>
                Don't have an account? <Link to="/register">Sign up</Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
